<template>
<v-layout row wrap>
<v-flex xs8 offset-xs2>
    <v-expansion-panel
      v-model="groups"
      expand
    >
      <v-expansion-panel-content
        v-for="item in stickers"
        :key="item.group"
      >
        <template v-slot:header>
          <div>#Category: {{ item.group }}</div>
        </template>
        <v-container
          fluid
          grid-list-lg
          >
          <v-layout row wrap>
            <v-flex xs3 v-for="s in item.data" :key="s.text">
              <v-card color="purple" class="white--text">
                <v-layout row>
                  <v-flex xs7>
                    <v-card-title primary-title>
                      <div>
                        <div class="headline fix-text">{{ s.text }}</div>
                      </div>
                    </v-card-title>
                  </v-flex>
                  <v-flex xs5>
                    <v-img
                      :src="`${s.url}`"
                      height="125px"
                      contain
                    ></v-img>
                  </v-flex>
                </v-layout>
                <v-divider light></v-divider>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-expansion-panel-content>
    </v-expansion-panel>
</v-flex>
</v-layout>
</template>

<script>
import stickers from '../plugins/stickers.json'
const prefix = '>'
export const find = (acc, cur, el) => acc.find(obj => obj[el] === cur[el])
export const flatten = groupBy => (acc, cur) => {
  const getNode = find(acc, cur, groupBy)

  getNode
    ? getNode.data.push({
        url: cur.url,
        // weight: cur.weight,
        text: cur.text
      })
    : acc.push({
        [groupBy]: cur[groupBy],
        data: [
          {
            url: cur.url,
            // weight: cur.weight,
            text: cur.text
          }
        ]
      })
  return acc
}


const order = (obj, groupBy) =>
{
  return obj.reduce(flatten(groupBy), [])
}

const sickersMap = stickers.map(l => {
  return { text: `${prefix}e ${l.text}`, group: l.group, url: l.url }
})

export default {
  name: 'commands',
  data: () => ({
    stickers: order(sickersMap, 'group'),
    groups: [],
    command: [],
    prefix
  }),
  methods: {
    FixPrefix (prefix) {
      return prefix.split('<prefix>').join(this.prefix)
    }
  }
}
</script>

<style scoped>
.fix-text {
  font-size: 18px !important
}
</style>
